<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <div class="btn_box">
        <Button type="primary" @click="handleOver(true)">批量通过</Button>
        <Button type="primary" @click="backCase(true)">批量驳回</Button>
      </div>
      <Table :columns="columns" :data="tableData">
        <template slot-scope="{ row }" slot="1">
          <span style="color:#538FFF">{{row['1']}}</span>
        </template>
        <template slot-scope="{row}" slot="action">
          <div>
            <button @click="handleLook(row)">查看</button>
            <button @click="handleOver(false,row)">通过</button>
            <button @click="backCase(false,row)">模板审核驳回</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage"
        :total="total"
        :page-size="pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <Modal v-model="backDilog" :footer-hide="true" title="驳回" ok-text="驳回">
      <Form ref="backFormValidate" :model="backForm" :rules="backFormRuleValidate" :label-width="0">
        <div style="margin-bottom:10px">请输入驳回原因：</div>
        <FormItem label prop="backInput">
          <Input
            v-model="backForm.backInput"
            :maxlength="100"
            show-word-limit
            type="textarea"
            placeholder="请输入原因"
            style="width:100%"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="backDilog=false">取消</Button>
        <Button type="primary" @click="backSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
const columns = [
  { type: "selection", width: 100, align: "center" },
  { title: "案号", slot: "1" },
  { title: "案件类型" },
  { title: "申请人" },
  { title: "代理人" },
  { title: "被申请人" },
  { title: "秘书" },
  { title: "文书类型" },
  { title: "创建时间" },
  { title: "审核状态" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
  components: {
    listSearch
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      searchData: {
        number: "",
        appeal: "",
        backAppeal: "",
        type: [],
        regName: [],
        caseType: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backAppeal",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "type",
          label: "文本类型",
          placeholder: "请选择文本类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "regName",
          label: "注册名称",
          placeholder: "请选择注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      columns: columns,
      tableData: [{ 1: 1 }],
      selectedData: [], // 批量同意
      selectedBack: [], // 批量驳回
      backDilog:false,
      // 驳回 
      backForm: { backInput: "" },
      backFormRuleValidate: {
        backInput: [
          { required: true, message: "请输入原因", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    goSearch() {},
    handleLook(row) {},
    // value true 批量  false 单个
    handleOver(value, row) {
      if (value) {
        if (this.selectedData.length === 0) {
          this.$Message.warning("请至少选择一条案件信息");
        //   return;
        }
      }

      this.$Modal.confirm({
          title: "操作",
          content:
            `<div style="color:#3B4453;text-align:center;font-size:18px">您确定要${value?'批量通过XX条':'xxx'} 案件裁决书吗?</div>`,
          onOk: () => {
            this.$Message.success("操作成功");
            this.$Message.error("系统出错，请稍后重试");
          },
          onCancel: () => {}
        });
    },
    // value true 批量  false 单个
    backCase(value, row) {
      if (value) {
        if (this.selectedBack.length == 0) {
          this.$Message.warning("请至少选择一条案件信息");
          return;
        }
        this.$Modal.confirm({
          title: "操作",
          content:
            '<div style="color:#3B4453;text-align:center;font-size:18px">您确定要批量驳回XX条案件裁决书吗?</div>',
          onOk: () => {
            this.$Message.success("操作成功");
            this.$Message.error("系统出错，请稍后重试");
          },
          onCancel: () => {}
        });
      } else {
        //   驳回
        this.backDilog=true
        // this.$Modal.confirm({
        //   title: "驳回",
        //   okText: '驳回',
        //   render: h => {
        //     return h("div", {
        //         style:{marginTop:'10px'}
        //     }, [
        //       h("Input", {
        //         props: {
        //           value: this.backInput,
        //           type: "textarea",
        //           autofocus: true,
        //           placeholder: "请输入驳回原因"
        //         },
        //         on: {
        //           input: val => {
        //             this.backInput = val;
        //           }
        //         }
        //       })
        //     ])
        //   },
        //   onOk: () => {
        //     this.$Message.success("操作成功");
        //     this.$Message.error("系统出错，请稍后重试");
        //   },
        // });
      }
    },
    //   驳回提交
    backSubmit() {
      this.$refs["backFormValidate"].validate(valid => {
        console.log(this.backForm);
        if (valid) {
          this.$Message.success("操作成功");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}

.wrap {
  .table_box {
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    .btn_box {
      display: flex;
      justify-content: flex-end;
      padding-right: 20px;
      margin-bottom: 20px;
      button:first-child {
        margin-right: 10px;
      }
    }
  }
}
</style>
